*
{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}

.main
{
    width:100%;
    height: 100vh;
    background: #6983aa;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;

}


.center
{
    height: 500px;
    width: 800px;
    display: flex;
    justify-content: center;
    background: white;
    border-radius: 15px;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
    overflow: auto;
    
    
}

h1{
    
    color: white;
   
    background: transparent;
    background-color: #8566aa;
    height: 50px;
    width:800px;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
    padding: 6px 0 2 px 0;
   /* margin-bottom: 10px;
    margin-top: 20px; */
    margin-left: -15px; 
    font-size: 40px;
}

.same
{
    width:100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

input
{
   text-align:center;
   height: 30px;
   top:10px;
   border:none;
   background: transparent;
   font-size: 20px;
   font-weight: 500;
   width:40%;
   border-bottom:  2px solid #8566aa;
   outline:none;
  /* margin-top:80px;
   margin-left:-400px; */

   
}
button
{
    height: 30px;
    width: 50px;
    border-radius: 20%;
    /*font-size: 40px;*/
    border-color: transparent;
    background-color: #8566aa;
    color:#fff;
    
    border:none;
    outline: none;
    margin-left:10px;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
    border-outline:none;

    
}

button:hover
{
    background-color: green;
}
ol{
    margin-top:3px;
}
ol li
{

    padding-left:0px;
    text-align: center;
    font-size: 500;
    min-height: 40px;
    display: flex;
    align-items: center;
    color:#8566aa;
    text-transform: capitalize;
    font-weight:bold;

}
.todo_style
{
    display:flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}
.todo_style .fa-times
{
     height: 20px;
    width: 20px;
    display: flex;
    justify-content: center;
    align-items: center;

    border-radius: 50%;
    border-color: transparent;
    background-color: #8566aa;
    color:#fff;
    margin:0 15px 0 35px;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
  
}
.todo_style .fa-times:hover{
    background-color: red;
}
@media (max-width:768px)
{
    .center 
    {
        width:55%;
    }
    h1
    {
        width:100%;
    }
    .todo_style .fa-times
    {
    margin:0 15px 0 15px;
    display:flex;
    justify-content: center;
    align-items: center;
    }
    buttonn
    {
        margin-left:5px;
        min-height:40px;
        width:40px;
        font-size: 30px;

    }

}
